.container {
  display: grid;
  grid-template-columns: 1fr 256px;
  grid-template-rows: 1fr 54px;
  min-height: 100vh;
}

.video {
  grid-column-start: 1;
  grid-column-end: 2;
}

.depthMap {
  grid-column-start: 2;
  grid-column-end: 3;
  align-self: center;
}

.footer {
  grid-row-start: 2;
  grid-column-start: 1;
  grid-column-end: 3;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}